import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        // 
        {
            path: '/',
            redirect: '/home',
        },
        // 
        {
            path: '/home',
            component: resolve => require(['@/components/home'], resolve),
        },
        // 
        {
            path: '/exercise',
            component: resolve => require(['@/components/exercise'], resolve),
        },
        // 
        {
            path: '/login',
            component: resolve => require(['@/components/login'], resolve),
        },
        // 
        {
            path: '/pending',
            name: 'pending',
            component: resolve => require(['@/components/pending'], resolve),
        },
        // 
        {
            path: '/operation',
            component: resolve => require(['@/components/operation'], resolve),
        },
        // =>
        {
            path: '/operation/set',
            component: resolve => require(['@/components/set'], resolve),
        },
        // =>
        {
            path: '/operation/state',
            component: resolve => require(['@/components/state'], resolve),
        },
        // =>
        {
            path: '/operation/calendar',
            component: resolve => require(['@/components/calendar'], resolve),
        },
        // 
        {
            path: '/award',
            name: 'award',
            component: resolve => require(['@/components/award'], resolve),
        },
        // =>
        {
            path: '/operation/offDuty',
            component: resolve => require(['@/components/offDuty'], resolve),
        }
    ]
})



