let port = 'https://demo.isaveu.cn';                // 
let urlPrefix = port + '/human/';
let ws = {};
import axios from 'axios';
var maAjax = axios;
import clientId_ from '@/components/global';
export default ({
    apiUrl : {
        // urlPrefix: port,
        login: port + '/human/api.login/index',   //  

        getting: urlPrefix + 'api.Usersetting/getting',    //  

        edit: urlPrefix + 'api.Usersetting/edit',    //  

        getMine: urlPrefix + 'api.User/getMine',    //  

        editWorkStatus: urlPrefix + 'api.User/editWorkStatus',    //  

        admit: urlPrefix + 'api.servicecall/admit',    //  

        info: urlPrefix + 'api.servicecall/info',    //   

        call_deal: urlPrefix + 'api.servicecall/call_deal',    //  

        getUserInfo: urlPrefix + 'api.servicecall/getUserInfo',    //  

        LoginOut: urlPrefix + 'api.watch/watch_Login_out',    //  
    },
    globalParam : {
        _token: window.localStorage.getItem("_token")
    },
    // 
    commonFun : {
        link: function(e){
            if(e.$router){
                e.$router.push(to);
            }
        },
        // 
        getCookie: function(cname){
            var name = cname + "=";
            var ca = document.cookie.split(';');
            for(var i=0; i<ca.length; i++) {
                var c = ca[i].trim();
                if (c.indexOf(name)==0) { return c.substring(name.length,c.length); }
            }
            return "";
        },
        // 
        returnStorageVal: function(name, val){
            var val = val;
            var newVal = window.localStorage.getItem(name, val);
            return newVal;
        },
        //   param : name - key string   val - value  - object,number,string
        setStorageVal: function(name, val){
            var val = val;
            var newVal = window.localStorage.setItem(name, val);
            return newVal;
        },
        clearStorageVal: function(name, val){
            window.localStorage.removeItem(name);
        },
        // ajax(dom,url,param,callback,type){
        ajax(jsonObj){
            var jsonObj = jsonObj;
            var dom = jsonObj.dom || "";
            var type = jsonObj.type || "get";
            var url = jsonObj.url || "";
            var param = jsonObj.data || {};
            var success = jsonObj.success || function(){};
            var isLoding = jsonObj.isLoding || false;
            var isLodingTit = jsonObj.isLodingTit || "";
            var error = jsonObj.error || function(response){
                // console.log('response.data',response)
                if(response.data) {
                    dom.$vux.toast.show({
                        type: 'text',
                        text: response.data.msg || "",
                        position: 'middle'
                    })
                } else {
                    dom.$vux.toast.show({
                        type: 'text',
                        text: response.msg || "",
                        position: 'middle'
                    })
                }
            };
            param.access_token = window.localStorage.getItem("access_token") || "";
            if(dom == ""){
                dom.$vux.toast.show({
                    type: 'text',
                    text: "this",
                    position: 'middle'
                })
                return;
            }
            if(url == ""){
                dom.$vux.toast.show({
                    type: 'text',
                    text: "",
                    position: 'middle'
                })
                return;
            }
            if(isLoding) {
                dom.$vux.loading.show({
                    text: isLodingTit
                })
            }
            if(type == "get") {
                dom.$ajax.get(url,{
                    params: param
                }).then(response =>{
                    if(response.data.code == 0) {
                        var data = response.data.data;
                        success(data,response.data);
                    } else {
                        if(response.data.code == 1) {
                            // window.localStorage.removeItem("access_token");
                            // localStorage.setItem("thisUrl", window.location.href);
                            // dom.$router.push('/no');
                        } else {
                            error(response);
                        }
                    }
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                }).catch(response => {
                    error(response);
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                });
            } else {
                dom.$ajax.post(url, dom.$qs.stringify(param)).then(response => {
                    if(response.data.code == 0) {
                        var data = response.data.data;
                        success(data,response.data);
                    } else {
                        if(response.data.code == 1 && response.data.msg == '') {
                            dom.$router.push('/login');
                            error(response);
                        }  else {
                            error(response);
                        }
                    }
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                }).catch(response => {
                    error(response)
                    if(isLoding) {
                        setTimeout(()=>{
                            dom.$vux.loading.hide()
                        }, lodingTime)
                    }
                })
            }

        },

        // 
        connect() {
            //websocket
            ws = new ReconnectingWebSocket("wss://demo.isaveu.cn:7272");
            // socket
            ws.onopen = this.onopen;
            // 
            ws.onmessage = this.onmessage;
            ws.onclose = function () {
                console.log("");
                this.connect();
            };
            ws.onerror = function () {
                console.log("");
            };
        },
        // 
        // onopen() {
        //     // 
        //     var login_data = '{"type":"login","user_id":"123","client_name":""}';
        //     // console.log("websocket:" + login_data);
        //     ws.send(login_data);
        // },
        // 
        onmessage(e) {
            console.log(e);
            // jsonjs
            var data = eval("(" + e.data + ")");
            var type = data.type || '';
            var token = window.localStorage.getItem("access_token");
            switch (type) {
                // Events.phpinitclient_iduid
                case 'init':
                    clientId_.clientId = data.client_id;
                    window.localStorage.setItem("clientId", data.client_id);
                    maAjax.post(port + '/human/api.index/bind',
                         {
                             client_id: data.client_id,
                             access_token: token
                         }).then(response=>{
                             console.log(response)
                     });
                    break;
                case 'watch_login'://
                    window.localStorage.setItem("access_token", data.access_token);
                    window.localStorage.setItem("user_id", data.user_id);
                    window.localStorage.setItem("login_time", new Date());
                    ws.send('{"type":"watch_login_on","user_id":"'+data.user_id+'"}');
                    window.location.href = '#/home';
                    break;
                case 'call'://
                    // alert(data.msg);
                    if(data.data.data.length > 0){
                        window.location.href = window.location.href + '?cont_id=' + data.data.service_cont_id + '&name=' + data.data.data[0].name + '&num=' + data.data.data.length + '&box=' + data.data.box_name;
                    }
                    if(data.data.data.length == 0){
                        window.location.href = window.location.href + '?cont_id=' + data.data.service_cont_id + '&name=' + '' + '&num=' + data.data.data.length + '&box=' + data.data.box_name;
                    }
                    console.log(data);
                    break;
                case 'pager'://
                    // alert(data.msg);
                    console.log(data);
                    if(data.data.data.length > 0){
                        console.log(111)
                        window.location.href = window.location.href + '?cont_id=' + data.data.service_cont_id + '&name=' + data.data.data[0].name + '&num=' + data.data.data.length + '&box=' + data.data.box_name;
                    }
                    if(data.data.data.length == 0){
                        console.log(222)
                        window.location.href = window.location.href + '?cont_id=' + data.data.service_cont_id + '&name=' + '' + '&num=' + data.data.data.length + '&box=' + data.data.box_name;
                    }
                    break;
                case 'alarm'://
                    console.log(data);
                    break;
                case 'admit'://
                    console.log(data);
                    break;
                // ping
                case 'ping':
                    var user_id = window.localStorage.getItem("user_id");
                    // console.log("type:pong");
                    ws.send('{"type":"pong","user_id":"'+user_id+'"}');
                    break;
                // mvcGatewayClientalert
                case 'watch_login_out':
                    if(data.getui_id != window.localStorage.getItem('Cid')){
                        localStorage.removeItem('access_token');
                        localStorage.removeItem('user_id');
                        localStorage.removeItem('login_time');
                        ws.send('{"type":"watch_login_out_ok","getui_id":"'+data.getui_id+'"');
                        window.location.href = '#/login';
                    }
                    break;
                // 
                default :
                    //console.log('mvcGatewayClient');
                    break;
            }
        }
    },
})



